.keyboard {
  background-color: hsl(223, 31%, 20%);
  border-radius: 20px;
  display: grid;
  padding: 20px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 50px);
  gap: 20px;
  &__key {
    min-width: 50px;
    font-size: 32px;
    background-color: hsl(30, 25%, 89%);
    border: none;
    border-radius: 10px;
    color: hsl(221, 14%, 31%);
    font-weight: 700;
    box-shadow: 0px 5px 0px 0px hsl(28, 16%, 65%);
    &:hover {
      opacity: 0.8;
    }
    &--clicked{
      filter: brightness(50%);
      
      
    }
    &--reset {
      grid-column: 1 / span 2;
      background-color: hsl(225, 21%, 49%);
      color: hsl(0, 0%, 100%);
      font-size: 20px;
      box-shadow: 0px 5px 0px 0px hsl(224, 28%, 35%);
    }
    &--equal {
      grid-column: 3 / span 2;
      color: hsl(0, 0%, 100%);
      font-size: 20px;
      background-color: hsl(6, 63%, 50%);
      box-shadow: 0px 5px 0px 0px hsl(6, 70%, 34%);
    }
    &--delete {
      background-color: hsl(225, 21%, 49%);
      color: hsl(0, 0%, 100%);
      font-size: 20px;
      box-shadow: 0px 5px 0px 0px hsl(224, 28%, 35%);
    }
  }


  &--theme2{
      background-color: hsl(0, 5%, 81%);
  .keyboard__key{
    background-color:hsl(45, 7%, 89%) ;
    box-shadow: 0px 5px 0px 0px hsl(35, 11%, 61%);
    &--delete, &--reset{
      background-color: hsl(185, 42%, 37%);
      box-shadow: 0px 5px 0px 0px hsl(185, 58%, 25%);
    }
    &--equal{
      background-color: hsl(25, 98%, 40%);
      box-shadow: 0px 5px 0px 0px hsl(25, 99%, 27%);
      color: hsl(0, 0, 100%)
    }
  }
  }

  &--theme3{
    background-color:hsl(268, 71%, 12%) ;
    .keyboard__key{
      background-color: hsl(281, 89%, 26%);
      box-shadow: 0px 5px 0px 0px hsl(285, 91%, 52%);
      color: hsl(52, 100%, 62%);
      &--delete, &--reset{
        color: hsl(0, 0%, 100%); 
      }
      &--equal{
        background-color: hsl(176, 100%, 44%);
        box-shadow: 0px 5px 0px 0px hsl(177, 92%, 70%);
        color: hsl(198, 20%, 13%)
      }
    }
  }
}
